import { fileUploadRequest, postBoardRequest, updateBoardRequest } from "apis";
import { PostBoardRequestDto, UpdateBoardRequestDto } from "apis/request/board";
import { ResponseDto } from "apis/response";
import {
  PostBoardResponseDto,
  UpdateBoardResponseDto,
} from "apis/response/board";
import { ChangeEvent, KeyboardEvent, useEffect, useRef, useState } from "react";
import { useCookies } from "react-cookie";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import {
  AUTH_PATH,
  BOARD_DETAIL_PATH,
  BOARD_PATH,
  BOARD_UPDATE_PATH,
  BOARD_WRITE_PATH,
  MAIN_PATH,
  SEARCH_PATH,
  USER_PATH,
} from "../../constant";
import { useBoardStore, useLoginUserStore } from "../../stores";
import "./style.css";
import Popup from "components/Popup";
import User from "views/User";

//          component:            //
export default function Header() {
  //          state: path variable          //
  const { userEmail } = useParams<string>();

  //          state:            //
  const { loginUser, setLoginUser, resetLoginUser } = useLoginUserStore();
  //          state: path          //
  const { pathname } = useLocation();
  //          state: cookie          //
  const [cookies, setCookie] = useCookies();
  //          state:           //
  const [isLogin, setLogin] = useState<boolean>(false);
  //          state:            //
  const [isAuthPage, setAuthPage] = useState<boolean>(false);
  //          state:            //
  const [isMainPage, setMainPage] = useState<boolean>(false);
  //          state:            //
  const [isSearchPage, setSearchPage] = useState<boolean>(false);
  //          state:             //
  const [isBoardDetailPage, setBoardDetailPage] = useState<boolean>(false);
  //          state:             //
  const [isBoardWritePage, setBoardWritePage] = useState<boolean>(false);
  //          state:             //
  const [isBoardUpdatePage, setBoardUpdatePage] = useState<boolean>(false);
  //          state:            //
  const [isUserPage, setUserPage] = useState<boolean>(false);
  //          state:            //
  const [isMyPage, setMyPage] = useState<boolean>(false);

  //          function:            //
  const navigate = useNavigate();

  //          event handler:               //
  const onLogClickHandler = () => {
    navigate(MAIN_PATH());
  };

  //          component:             //
  const SearchButton = () => {
    //          state:              //
    const searchButtonRef = useRef<HTMLDivElement | null>(null);
    //          state:            //
    const [status, setStatus] = useState<boolean>(false);
    //          state:           //
    const [word, setWord] = useState<string>("");
    //          state:  path variable          //
    const { searchWord } = useParams();

    //          event handler:              //
    const onSearchWordChangeHandler = (
      event: ChangeEvent<HTMLInputElement>
    ) => {
      const value = event.target.value;
      setWord(value);
    };
    //          event handler:              //
    const onSearchWordKeyDownHandler = (
      event: KeyboardEvent<HTMLInputElement>
    ) => {
      if (event.key !== "Enter") return;
      if (!searchButtonRef.current) return;
      searchButtonRef.current.click();
    };
    //          event handler:               //
    const onSearchButtonClickHandler = () => {
      if (!status) {
        setStatus(!status);
        return;
      }
      navigate(SEARCH_PATH(word));
    };

    //          effect:  path variable              //
    useEffect(() => {
      if (searchWord) {
        setWord(searchWord);
        setStatus(true);
      }
    }, [searchWord]);

    if (!status)
      //          render:     ( false )         //
      return (
        <div className="icon-button" onClick={onSearchButtonClickHandler}>
          <div className="icon search-light-icon"></div>
        </div>
      );
    //          render:     ( true )         //
    return (
      <div className="header-search-input-box">
        <input
          className="header-search-input"
          type="text"
          placeholder=" ."
          value={word}
          onChange={onSearchWordChangeHandler}
          onKeyDown={onSearchWordKeyDownHandler}
        />
        <div
          ref={searchButtonRef}
          className="icon-button"
          onClick={onSearchButtonClickHandler}
        >
          <div className="icon search-light-icon"></div>
        </div>
      </div>
    );
  };

  //          component:             //
  const UserButton = () => {
    //          state:            //
    const [isPopupOpen, setIsPopupOpen] = useState(false);

    const openPopup = () => {
      setIsPopupOpen(true);
    };

    const closePopup = () => {
      setIsPopupOpen(false);
    };

    const onUpdateEventHandler = () => {
      if (!loginUser) return;
      navigate(USER_PATH(loginUser?.email));
    };

    //          render:             //
    return (
      <div className="icon-button" onClick={openPopup}>
        <div className="icon profile-user-icon"></div>
        {isPopupOpen && loginUser !== null && (
          <Popup
            user={loginUser}
            onClose={closePopup}
            onUpdate={onUpdateEventHandler}
          />
        )}
      </div>
    );
  };

  //          component:             //
  const MyPageButton = () => {
    //          state: userEmail path variable          //
    const { userEmail } = useParams();

    //          event handler:               //
    const onMyPageButtonClickHandler = () => {
      if (!loginUser) return;
      const { email } = loginUser;
      navigate(USER_PATH(email));
    };
    //          event handler:               //
    const onSignOutButtonClickHandler = () => {
      resetLoginUser();
      setCookie("accessToken", "", { path: MAIN_PATH(), expires: new Date() });
      navigate(MAIN_PATH());
    };

    //          event handler:               //
    const onSignInButtonClickHandler = () => {
      navigate(AUTH_PATH());
    };

    //          render:            //
    if (isLogin && userEmail === loginUser?.email)
      return (
        <div
          className="white-button"
          onClick={onSignOutButtonClickHandler}
        >{``}</div>
      );
    //          render:            //
    if (isLogin)
      return (
        <div
          className="white-button"
          onClick={onMyPageButtonClickHandler}
        >{``}</div>
      );
    //          render:            //
    return (
      <div
        className="black-button"
        onClick={onSignInButtonClickHandler}
      >{``}</div>
    );
  };
  //          component:             //
  const UploadButton = () => {
    //          state:   path variable          //
    const { boardNumber } = useParams();

    //          state:            //
    const { title, content, boardImageFileList, resetBoard } = useBoardStore();

    //          function: post board response            //
    const postBoardResponse = (
      responseBody: PostBoardResponseDto | ResponseDto | null
    ) => {
      if (!responseBody) return;
      const { code } = responseBody;

      if (code === "DBE") alert(" ..");
      if (code === "AF" || code === "NU") navigate(AUTH_PATH());
      if (code === "VF") alert("  .");
      if (code !== "SU") return;

      resetBoard();
      if (!loginUser) return;
      const { email } = loginUser;
      navigate(USER_PATH(email));
    };

    //          function: update board response            //
    const updateBoardResponse = (
      responseBody: UpdateBoardResponseDto | ResponseDto | null
    ) => {
      if (!responseBody) return;
      const { code } = responseBody;

      if (code === "DBE") alert(" ..");
      if (code === "AF" || code === "NU" || code === "NB" || code === "NP")
        navigate(AUTH_PATH());
      if (code === "VF") alert("  .");
      if (code !== "SU") return;

      if (!boardNumber) return;
      navigate(BOARD_PATH() + "/" + BOARD_DETAIL_PATH(boardNumber));
    };

    //          event handler:               //
    const onUploadButtonClickHandler = async () => {
      const accessToken = cookies.accessToken;
      if (!accessToken) return;

      const boardImageList: string[] = [];

      for (const file of boardImageFileList) {
        const data = new FormData();
        data.append("file", file);

        const url = await fileUploadRequest(data);
        if (url) boardImageList.push(url);
      }
      const isWritePage = pathname === BOARD_PATH() + "/" + BOARD_WRITE_PATH();
      if (isWritePage) {
        const requestBody: PostBoardRequestDto = {
          title,
          content,
          boardImageList,
        };

        postBoardRequest(requestBody, accessToken).then(postBoardResponse);
      } else {
        if (!boardNumber) return;

        const requestBody: UpdateBoardRequestDto = {
          title,
          content,
          boardImageList,
        };

        updateBoardRequest(boardNumber, requestBody, accessToken).then(
          updateBoardResponse
        );
      }
    };

    //          render:              //
    if (title && content)
      return (
        <div
          className="black-button"
          onClick={onUploadButtonClickHandler}
        >{``}</div>
      );
    //          render:               //
    return <div className="disable-button">{``}</div>;
  };

  //          effect: path               //
  useEffect(() => {
    const isAuthPage = pathname.startsWith(AUTH_PATH());
    setAuthPage(isAuthPage);
    const isMainPage = pathname === MAIN_PATH();
    setMainPage(isMainPage);
    const isSearchPage = pathname.startsWith(SEARCH_PATH(""));
    setSearchPage(isSearchPage);
    const isBoardDetailPage = pathname.startsWith(
      BOARD_PATH() + "/" + BOARD_DETAIL_PATH("")
    );
    setBoardDetailPage(isBoardDetailPage);
    const isBoardWritePage = pathname.startsWith(
      BOARD_PATH() + "/" + BOARD_WRITE_PATH()
    );
    setBoardWritePage(isBoardWritePage);
    const isBoardUpdatePage = pathname.startsWith(
      BOARD_PATH() + "/" + BOARD_UPDATE_PATH("")
    );
    setBoardUpdatePage(isBoardUpdatePage);
    const isUserPage = pathname.startsWith(USER_PATH(""));
    setUserPage(isUserPage);
    if (!loginUser) {
      setMyPage(false);
    } else {
      const isMyPage = pathname.startsWith(USER_PATH(loginUser.email));
      setMyPage(isMyPage);
    }
  }, [pathname]);

  //          effect:                 //
  useEffect(() => {
    setLogin(loginUser !== null);
  }, [loginUser]);

  //          render:            //
  return (
    <div id="header">
      <div className="header-container">
        <div className="header-left-box" onClick={onLogClickHandler}>
          <div className="icon-box">
            <div className="icon logo-dark-icon"></div>
          </div>
          <div className="header-logo"> {"Become Board"}</div>
        </div>

        <div className="header-right-box">
          {(isAuthPage || isMainPage || isSearchPage || isBoardDetailPage) && (
            <SearchButton />
          )}
          {isUserPage && isLogin && userEmail === loginUser?.email && (
            <UserButton />
          )}
          {(isMainPage || isSearchPage || isBoardDetailPage || isUserPage) && (
            <MyPageButton />
          )}
          {(isBoardWritePage || isBoardUpdatePage) && <UploadButton />}
        </div>
      </div>
    </div>
  );
}
