import React, { useEffect } from "react";
import "./style.css";
import { useLoginUserStore } from "stores";

//          component:            //
export default function Footer() {
  //          state:            //
  const { loginUser, resetLoginUser } = useLoginUserStore();

  //          event handler:               //
  const onInstaIconButtonClickHandler = () => {
    window.open("https://www.instagram.com");
  };

  //          event handler:                //
  const onNaverBlogIconButtonClickHandler = () => {
    window.open("https://blog.naver.com");
  };

  //          event handler: E-mail Link             //
  const onEmailIconButtonClickHandler = () => {
    window.open("http://www.becomesolution.com/");
  };

  //          effect :                //
  useEffect(() => {
    if (loginUser === null) {
      resetLoginUser();
    }
  }, [loginUser]);

  //          render:            //
  return (
    <div id="footer">
      <div className="footer-container">
        <div className="footer-top">
          <div className="footer-logo-box">
            <div className="icon-box">
              <div className="icon logo-light-icon"></div>
            </div>
            <div className="footer-logo-text">{`Become Board`}</div>
          </div>
          <div className="footer-link-box">
            <div
              className="footer-email-link"
              onClick={onEmailIconButtonClickHandler}
            >
              {loginUser?.email}
            </div>
            <div
              className="icon-button"
              onClick={onInstaIconButtonClickHandler}
            >
              <div className="icon insta-icon"></div>
            </div>
            <div
              className="icon-button"
              onClick={onNaverBlogIconButtonClickHandler}
            >
              <div className="icon naver-blog-icon"></div>
            </div>
          </div>
        </div>
        <div className="footer-botton">
          <div className="footer-copyright">{`Copyright  2025 BECOME SOLUTION. All Rights Reserved`}</div>
        </div>
      </div>
    </div>
  );
}
